import React, { useState, useEffect } from 'react';
import { theme } from '../../theme.js'

const services = theme.services.services

const containerStyle = {
  maxWidth: '1200px',
  margin: '0 auto',
  padding: '20px',
  textAlign: 'center',
  backgroundColor: theme.colors.background,
  borderRadius:10
};

const headingStyle = {
  fontWeight: 'bold',
  color: theme.colors.primary,
  fontFamily: theme.fonts.primary,
  marginBottom: '40px',
  fontSize: '2rem'
};

const rowStyle = {
  display: 'flex',
  alignItems: 'center',
  justifyContent: 'space-between',
  margin: '0 100px 0 100px',
  flexWrap: 'wrap',
  flexDirection: 'row-reverse'
};

const titleStyle = {
  color: theme.colors.primary,
  fontFamily: theme.fonts.primary,
  fontSize: '1.2rem',
  textAlign: 'left'
};

const descriptionStyle = {
  flex: 1,
  padding: '20px',
};

const imageStyle = {
  flex: 1,
  textAlign: 'center',
};

const imgStyle = {
  width: '250px',
  height: 'auto',
  borderRadius: '8px',
};

const ulStyle = {
  listStylePosition: 'inside',
  padding: '0',
};

const liStyle = {
  marginBottom: '5px',
  color: theme.colors.primary,
  fontFamily: theme.fonts.primary,
  fontSize: '1rem',
  textAlign: 'left',
  paddingLeft: '1em', // Add padding to align text properly
  textIndent: '-1em', // Negative indent to pull the first line back
  marginLeft: '1em', // Adjust margin to properly align bullets
  // listStylePosition: 'inside', // Ensure bullets are inside the padding
  lineHeight: '1.5',

};

const mobileRowStyle = {
  flexDirection: 'column',
  margin:0
};

const mobileDescriptionStyle = {
  paddingTop: '20px',
};

const Services = () => {
  const [isMobile, setIsMobile] = useState(false);

  useEffect(() => {
    const handleResize = () => {
      setIsMobile(window.innerWidth <= 600);
    };
    handleResize();
    window.addEventListener('resize', handleResize);
    return () => window.removeEventListener('resize', handleResize);
  }, []);

  return (
    <div style={containerStyle}>
      <p style={headingStyle}>{theme.services.title}</p>
      {services.map((service, index) => (
        <div
          key={index}
          style={{
            ...rowStyle,
            ...(isMobile ? mobileRowStyle : {}),
            ...(isMobile ? {} : (index % 2 === 0 ? {} : { flexDirection: 'row' })),
          }}
        >
          <div style={imageStyle}>
            <img src={service.image} alt={service.name} style={imgStyle} />
          </div>
          <div style={{
            ...descriptionStyle,
            ...(isMobile ? mobileDescriptionStyle : {}),
          }}>
            <h1 style={titleStyle}>{service.name}</h1>
            <ul style={ulStyle}>
              {service.description.map((point, idx) => (
                <li key={idx} style={liStyle}>{point}</li>
              ))}
            </ul>
          </div>
        </div>
      ))}
    </div>
  );
};

export default Services;
